<template>
	<div class="home">
		
		<Header v-show = "$route.name != 'SignupForm' && $route.name != 'SigninForm' && $route.name != 'Signupformstep2'"/>
		<!-- END: Mobile Menu -->
        
		<router-view/>
		<Footer v-show = "$route.name != 'SignupForm' && $route.name != 'SigninForm' && $route.name != 'Signupformstep2'" />
	</div>
</template>

<script>

import Header from '@/components/Header.vue';

import Footer from '@/components/Footer.vue';


export default {
	name:"main_page",

	components: {
	    
		Header,
		
		Footer
	},
	data () {
    return {
      msg: window.location.hash
    }
  }
}
</script>


