import Vue from 'vue'
import Router from 'vue-router'




Vue.use(Router)

const routes = [
 {
  path:'',
  name:'HomePage',
  component:()=>import('../pages/HomePage.vue'),
 },
 {
  path:'/register-form',
  name:'SignupForm',
  component:()=>import('../pages/RegisterForm.vue'),
 },
 
 {
  path:'/sign-up-step2/:id',
  name:'Signupformstep2',
  component:()=>import('../pages/Signupformstep2.vue'),
 },
 {
  path:'/signin-form',
  name:'SigninForm',
  component:()=>import('../pages/SigninForm.vue'),
 }



]



export default new Router({

  routes
})



